import './ScreenPortfolioPage.scss';

import React, { useState, useEffect, } from 'react';
import { useTranslation } from 'react-i18next';

import CommonButton from '../components/CommonButton';
import InnerLink from '../components/InnerLink';

import { portfolioTags, portfolioList, } from '../data/portfolio';



export default function ScreenPortfolioPage(props) {
  const { i18n, t, } = useTranslation('portfolio');
  const [currentTag, setCurrentTag] = useState({ id: 'all', name: 'All', });

  const [posts, setPosts] = useState(portfolioList);
  useEffect(() => {
    const posts = portfolioList.filter((item) => item.tags.includes(currentTag.id));
    setPosts(posts);
  }, [currentTag]);

  return (
    <section id='portfolio-page' className='section-portfolio-page'>
      <div className='round-portfolio round-portfolio-1'></div>
      <div className='round-portfolio round-portfolio-2'></div>
      <div className='container'>
        {/*<h2 className='title'>{t('Portfolio')}</h2>*/}
        {/*<p className='desc'>{t('portfolioDescription')}</p>*/}
        <div className='portfolio-page__tabs'>
          {portfolioTags.map((item) =>
            <CommonButton
              key={item.id}
              className='portfolio-page__tab'
              size='small'
              variant={currentTag.id === item.id ? 'gradient' : 'base'}
              active={currentTag.id === item.id ? true : false}
              onClick={() => setCurrentTag(item)}
            >
              {t(item.name)}
            </CommonButton>
          )}
        </div>

        <div className='row portfolio-page'>
          {posts.map((item) => {
            return (
              <div
                key={item.id}
                className='col-12'
              >
                <div className='portfolio-page'>
                  <div className='portfolio-page-item'>
                    {item.img && <span className='portfolio-page-item__img'>
                      {item.img(item, i18n)}
                    </span>}
                    {/*<div className='portfolio-page-item__date'>
                      <span>{new Date(item.date).toLocaleDateString(i18n.language)}</span>
                    </div>*/}
                    <div className='portfolio-page-item__title'>{item.title[i18n.language]}</div>
                    <div className='portfolio-page-item__desc'>
                      {item.desc[i18n.language]}
                    </div>
                    <div className='portfolio-page-item__link'>
                      <InnerLink
                        to={item.link}
                        text={t('Read more')}
                      />
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>

        {/*<div
          className='portfolio-page__more'
        //className='portfolio-page__more d-md-none'
        >
          <Link to='/portfolio'>
            <CommonButton
              className=''
              size='full'
              variant='gradient'
            >
              {t('More')}
            </CommonButton>
          </Link>
        </div>*/}
      </div>
    </section>
  );
};
